
/*================== service ====================*/
.service {
    background: -webkit-gradient(
        linear, left bottom, right top, from(#eee9de),color-stop(10%, #eee9de), color-stop(10%, #000000), 
        color-stop(10%, white),color-stop(84%, white),color-stop(84%, #000000),
        color-stop(84%, #eee9de),to(#eee9de));
    background: -webkit-linear-gradient(
        bottom left, #eee9de 0%,#eee9de 10%, #000000 10%, 
        white 10%,white 84%,#000000 84%,
        #eee9de 84%,#eee9de 100%);
    background: -o-linear-gradient(
        bottom left, #eee9de 0%,#eee9de 10%, #000000 10%, 
        white 10%,white 84%,#000000 84%,
        #eee9de 84%,#eee9de 100%);
    background: linear-gradient(
        to top right, #eee9de 0%,#eee9de 10%, #000000 10%, 
        white 10%,white 84%,#000000 84%,
        #eee9de 84%,#eee9de 100%);
}
.services-text-box {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0 auto;
    width: 60%;
}
.services-text-box p {
    padding: 0 2%;
    text-align: left;
}
.serviceBoxes {
    display: inline-block;
    margin-top: 10vh;
    text-align: center;
    width: 20%;
    min-width: 300px;
}
.serviceCircle {
    border-radius: 50%;
    height: 10vw;
    position: relative;
    margin: 0 auto;
    width: 10vw;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.icon {
    text-align: center;
    position: relative;
    top: 20%;
    width:60%;
}
.serviceTitle {
    color: #448888;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 1px;
    padding: 10px 0px 30px 0px;
}
.serviceBorder {
    border-bottom: 1px solid brown;
    margin: 0px 5vw;
}
.serviceText {
    color: silver;
    margin: 0 auto;
    text-align: center;
    padding: 30px 1vw 10vh 1vw;
    width: 70%;
}
.webDes {
    color: #d16455;
}
.webDev {
    color: #714089;
}
.backend {
    color: #1ec503;
}
.socialMedia {
    color: #58b2d8;
}


.dark-bg {
    background: rgba(0, 0, 0, 0.6);
    color: white;
}

@media (max-width: 600px) {
  .service {
    padding-top: 4vh;
  }  
  .serviceBoxes {
    position: relative;
    min-width: none;
    width: 90%;
  }
  .serviceCircle {
    margin: 0 5 0 0;
    position: absolute;
    height: 14vw;
    width: 14vw;
  }
  .serviceTitle {
    float: right;
    margin-right: 0;
    margin-top: 0;
    padding: 0;
    position: relative;
    text-align: left;
    width: 80%;
  }
  .serviceBorder {
    display: none;
  }
  .serviceText {
    float: right;
    margin-right: 0;
    margin-top: 0;
    padding: 0;
    position: relative;
    text-align: left;
    width: 80%;
  }
}


